@keyframes section-top-img {
  0% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-10%);
  }
  100% {
    transform: translate(0);
  }
}
@keyframes section-left-img {
  0% {
    transform: translateX(0);
  }
  50% {
    transform: translateX(-10%);
  }
  100% {
    transform: translate(0);
  }
}
@keyframes jello-horizontal {
  0% {
    transform: scale3d(1, 1, 1);
  }
  30% {
    transform: scale3d(1.25, 0.75, 1);
  }
  40% {
    transform: scale3d(0.75, 1.25, 1);
  }
  50% {
    transform: scale3d(1.15, 0.85, 1);
  }
  65% {
    transform: scale3d(0.95, 1.05, 1);
  }
  75% {
    transform: scale3d(1.05, 0.95, 1);
  }
  100% {
    transform: scale3d(1, 1, 1);
  }
}
@keyframes capabilities-item-svg {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.2);
  }
  100% {
    transform: scale(1);
  }
}
body,
html {
  position: relative;
  overflow-x: hidden;
  background: #fff;
  font: 500 14px/1.2 "Montserrat", sans-serif;
  color: #474747;
  min-height: 100vh;
}

h2 {
  font-weight: 700;
  font-size: 42px;
  text-transform: uppercase;
  color: #000;
  line-height: 1.2;
}

a {
  transition: all 0.3s;
}
a:hover {
  color: #6c96f9 !important;
}

.btn__blue {
  width: max-content;
  border: none;
  border-radius: 8px;
  padding: 19px 46px;
  background: #6c96f9;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: 500;
  font-size: 16px;
  line-height: 1.5;
  text-align: center;
  color: #fff;
  transition: all 0.3s;
}
.btn__blue:hover {
  background: #4c81ff;
}

.container {
  max-width: 1340px;
  margin: 0 auto;
  box-sizing: content-box;
  padding: 0 20px;
}

.nav {
  position: absolute;
  top: 0;
  left: 0;
  width: 100vw;
  padding: 22px 0;
  z-index: 3;
}
.nav__hum {
  display: none;
}
.nav .container {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  gap: 35px;
}
.nav ul {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 36px;
  font: 500 14px/1.64286 "Montserrat", sans-serif;
  color: #474747;
  margin: 0 0 0 auto;
}
.nav ul a {
  transition: all 0.3s;
  color: #474747;
}
.nav__btn {
  font-weight: 500;
  font-size: 14px;
  line-height: 1.71429;
  text-align: center;
  color: #474747;
  background: transparent;
  border: 3px solid #6c96f9;
  border-radius: 8px;
  padding: 7px 17px;
  transition: all 0.3s;
}
.nav__btn:hover {
  opacity: 0.7;
}
.nav__logo {
  max-width: 243px;
}
.nav__logo img {
  width: 100%;
}

.header {
