  grid-template-columns: 24px 1fr;
}
.advant__item ul li::before {
  content: "";
  display: block;
  width: 3px;
  height: 3px;
  border-radius: 100%;
  margin: 10px auto 0;
  background: #474747;
}
.advant__item p {
  margin: 15px 0 0;
