}
.advant__logo img {
  animation: jello-horizontal 2s both infinite;
}
.advant__logo::after {
  content: "";
  height: 100px;
  width: 75px;
  border-radius: 100%;
  border: 1px solid #6c96f9;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) rotate(-45deg);
  z-index: -1;
}
.advant__logo::before {
  content: "";
  height: 100px;
  width: 75px;
  border-radius: 100%;
  background: linear-gradient(180deg, #f3f7ff 0%, #dae5ff 100%);
