  font-size: 14px;
  letter-spacing: 0.02em;
  color: #474747;
}
.comment__text {
  margin: 20px 0 0;
  height: 132px;
  overflow-y: auto;
}
.comment__text p {
  font-weight: 400;
  font-size: 18px;
