.comment__arrow .swiper-button-prev,
.comment__arrow .swiper-button-next {
  position: relative;
  display: block;
  margin: 0;
  transform: none;
  inset: auto;
  width: 40px;
  height: 40px;
  border-radius: 100%;
  background: #F9FBFF;
  display: flex;
  justify-content: center;
  align-items: center;
}
.comment__arrow .swiper-button-prev::after,
.comment__arrow .swiper-button-next::after {
  content: none;
}
.comment__arrow .swiper-button-prev svg,
.comment__arrow .swiper-button-next svg {
