  grid-template-columns: 1fr;
  gap: 20px;
}
.quest__item {
  border-radius: 16px;
  background: #f2f6ff;
  padding: 20px;
}
.quest__text {
  max-height: 0;
  overflow: hidden;
}
.quest__text p {
  margin: 20px 0 0;
  font-weight: 400;
  font-size: 18px;
  letter-spacing: 0.02em;
  color: #474747;
}
.quest__btn {
  display: grid;
  grid-template-columns: 1fr 20px;
  gap: 20px;
  align-items: start;
  cursor: pointer;
}
.quest__btn p {
  font-weight: 700;
  font-size: 24px;
  letter-spacing: 0.02em;
  color: #000;
}
.quest__btn svg {
