}
.footer__contact {
  grid-column: 3/4;
  grid-row: 1/3;
  display: grid;
  gap: 10px;
}
.footer__contact a {
  display: grid;
  grid-template-columns: 20px 1fr;
  gap: 10px;
  align-items: center;
  font-weight: 500;
  font-size: 14px;
  line-height: 1.64286;
  color: #fff;
}
.footer__desc {
  grid-column: 1/2;
  grid-row: 2/3;
  font-weight: 500;
  font-size: 14px;
  line-height: 1.64286;
  color: #fff;
}

@media (max-width: 1200px) {
  .nav .container {
    gap: 15px;
  }
  .nav ul {
    gap: 15px;
  }
  .nav__logo {
    max-width: 200px;
  }
  .footer .container {
    grid-template-columns: 300px 1fr auto;
  }
  .footer__logo {
    max-width: 250px;
  }
}
@media (max-width: 1024px) {
  .nav ul, .nav__btn {
    display: none;
  }
  .nav .container {
    flex-wrap: wrap;
  }
  .nav__hum {
    width: 25px;
    height: 18px;
    position: relative;
    display: block;
    cursor: pointer;
    margin: 0 0 0 auto;
  }
  .nav__hum span, .nav__hum::before, .nav__hum::after {
    content: "";
    display: block;
    width: 100%;
    position: absolute;
    top: 0;
    left: 0;
    height: 2px;
    border-radius: 2px;
    background: #6C96F9;
  }
  .nav__hum::before {
    top: 50%;
  }
  .nav__hum span {
    top: 100%;
  }
  .nav.active {
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background: linear-gradient(10deg, #c9deff 0%, #f7faff 100%);
    display: grid;
  }
  .nav.active .container {
    height: 100%;
    width: 100%;
    display: grid;
    grid-template-columns: 1fr auto;
    justify-content: center;
    align-items: center;
    box-sizing: border-box;
    grid-template-rows: auto 1fr auto;
  }
  .nav.active ul {
    display: grid;
    grid-template-columns: 1fr;
    gap: 28px;
    width: 100%;
    margin: auto 0;
    grid-column: 1/3;
    justify-items: center;
  }
  .nav.active ul a {
    font-weight: 700;
    font-size: 24px;
    text-transform: uppercase;
    color: #000;
    text-align: center;
  }
  .nav.active .nav__hum span {
    opacity: 0;
  }
  .nav.active .nav__hum::before, .nav.active .nav__hum::after {
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%) rotate(45deg);
  }
  .nav.active .nav__hum::after {
    transform: translate(-50%, -50%) rotate(-45deg);
  }
  .nav.active .nav__btn {
    display: flex;
    width: 100%;
    font-weight: 500;
    font-size: 16px;
    line-height: 1.5;
    text-align: center;
    color: #fff;
    padding: 18px;
    border: none;
    background: #6c96f9;
    border-radius: 8px;
    text-align: center;
    justify-content: center;
    align-items: center;
    grid-column: 1/3;
    opacity: 1;
  }
  .nav.active .nav__btn:hover {
    background: #4c81ff;
    opacity: 1;
  }
  .header {
    background: url("../img/header_bg_mob.jpg") top no-repeat;
    background-size: cover;
    padding: 85px 0 350px;
  }
  .header__img {
    width: 470px;
    bottom: auto;
    left: calc(50% - 235px);
    top: calc(100% + 20px);
  }
  .advant {
    padding: 50px 0;
  }
  .advant__wrapper {
    grid-template-columns: 1fr 1fr;
  }
  .set {
    padding: 50px 0;
  }
  .set__bg {
    display: none;
  }
  .set__wrapper {
    grid-template-columns: 1fr;
  }
  .func {
    padding: 50px 0;
  }
  .func__wrapper {
    grid-template-columns: 1fr;
  }
  .func__item {
    width: 100% !important;
  }
  .optimal {
    padding: 50px 0;
  }
  .optimal .container {
    grid-template-columns: 1fr;
    gap: 20px;
  }
  .optimal__bg {
    max-width: 500px;
    margin: 0 auto;
    order: 2;
  }
  .comment {
    padding: 50px 0;
  }
  .mail .container {
    grid-template-columns: 1fr;
    gap: 30px;
  }
  .footer .container {
    grid-template-columns: auto 1fr auto;
  }
